/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .mkdf-widget-title-holder {
        margin-bottom: $widget-title-margin;

        .mkdf-widget-title {
            color: $widget-heading-color;
            margin: 0 0 3px;
        }

        .mkdf-separator {
            border-color: $widget-text-color;
            border-bottom-width: 3px;
            width: 30px;
        }
    }

    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:first-child{
                padding-top: 0;
            }

            &:last-child{
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            margin-top: 10px;
            margin-left: 10px;
        }
    }

    // widgets with lists and animation on link hover
    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {

        .mkdf-widget-title {
            margin-bottom: 0;
        }

        ul {

            li {
                overflow: hidden;
                font-style: italic;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    @include mkdfTransition(padding .2s ease);
                    color: $default-text-color;

                    &:after {
                        content: "\35";
                        display: inline-block;
                        font-size: 12px;
                        font-family: "ElegantIcons";
                        left: -15px;
                        line-height: 13px;
                        position: absolute;
                        speak: none;
                        text-rendering: auto;
                        text-transform: none;
                        top: 50%;
                        vertical-align: middle;

                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;

                        @include mkdfTransform(translateY(-50%));
                        @include mkdfTransition(left .2s ease);
                    }

                    &:hover {
                        padding-left: 15px;

                        &:after {
                            left: -5px;
                        }
                    }
                }
            }
        }
    }

    // widgets with lists and w/o animation on link hover
    &.widget_recent_comments,
    &.widget_rss {

        .mkdf-widget-title {
            margin-bottom: 0;
        }
    }

    // calendar
    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: $widget-text-color;
            background-color: $widget-list-item-background-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // rss
    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    // search
    &.widget_search {

        .input-holder {
            border: none;
            background-color: #f2f2f2;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $default-heading-color;
                display: inline-block;
                float: left;
                font-family: $default-heading-font;
                font-size: 12px;
                font-weight: 600;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include mkdfTransition(color .2s ease-in-out);
            }

            input.search-field {
                width: calc(100% - #{$widget-search-form-height});
                padding: 0 0 0 21px;

                @include mkdfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                color: $widget-text-color;
                cursor: pointer;
                font-size: 16px;
                width: $widget-search-form-height;
                padding: 0;
                @include mkdfTransition(all .2s ease-in-out);

                &:hover {
                    color: $second-main-color;
                    @include mkdfTransition(all .2s ease-in-out);
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    // search post type
    &.mkdf-search-post-type-widget {
        .mkdf-post-type-search-field {
            border-radius: 3px;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            background-color: transparent;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 12px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            position: relative;
            vertical-align: top;
            -webkit-appearance: none;
            padding: 0 25px 0 20px;
            width: 100%; /* width of loading icon */

            @include mkdfTransition(color .2s ease-in-out);

            @include mkdfPlaceholder {
                color: $widget-text-color;
            }
        }
    }

    // combobox
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include mkdfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    &.widget_categories{
        ul:not(.children){
            margin-top: -6px;
        }
    }

    // tag cloud
    &.widget_tag_cloud {

        .mkdf-widget-title-holder{
            margin-bottom: 17px;
        }

        a {
            color: $widget-text-color;
            display: inline-block;
            font-size: 15px !important;
            font-family: $default-text-font;
            font-style: italic;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            margin: 0 3px 0 0;
            padding: 0;
            text-decoration: underline;

            &:not(:last-child){
                &:after{
                    content: ', ';
                    position: relative;
                    right: 0;
                    bottom: 0;
                }
            }

            &:hover {
                color: $second-main-color;
            }
        }
    }

    &.mkdf-recent-post-widget{
        .mkdf-recent-posts{
            .mkdf-rp-item{
                margin: 0 0 10px;
                padding: $widget-list-item-padding;

                a{
                    display: table;
                    width: 100%;

                    .mkdf-rp-image,
                    .mkdf-rp-title{
                        display: table-cell;
                        vertical-align: middle;
                    }

                    .mkdf-rp-title{
                        @include mkdfTransition(color .2s ease-in-out);
                        font-weight: 600;
                    }

                    .mkdf-rp-image{
                        padding-right: 20px;
                        width: 56px;
                        height: 56px;
                        img{
                            display: block;
                        }
                    }

                    &:hover{
                        .mkdf-rp-title{
                            color: $default-text-color;
                        }
                    }
                }
            }
        }
    }
}