@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 32px 0 20px;
    }
	
	.mkdf-comment {
		
		&.mkdf-pingback-comment {
			
			.mkdf-comment-text {
				padding: 0;
			}
		}
	}

    .mkdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 5px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 110px;
        height: 110px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 130px;
        min-height: 110px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 5px 0 1px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: left;
            margin: 4px 10px 0 0;
            font-size: 14px;
            font-weight: 700;
            color: $default-heading-color;

            &:hover {
                color: $first-main-color;
            }
        }

        .comment-reply-link{
            &:after{
                content: "\f075";
                font-size: 12px;
                font-family: "FontAwesome";
                position: relative;
                padding-left: 5px;
            }
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            margin: 0;
            font-size: 12px;
            font-family: $default-heading-font;
            font-weight: 600;
            padding-bottom: 4px;
        }

        .mkdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            font-size: 14px;
            font-weight: 700;
            color: $default-heading-color;

            &:hover {
                color: $first-main-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .mkdf-comment-form-inner {
        padding: 32px 0 48px;
    }
    
    .comment-reply-title {
        margin: 0 0 45px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {
	        
            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include mkdfButtonDefaultStyle();
	@include mkdfButtonMediumParams();
	@include mkdfButtonSolidColor();
	cursor: pointer;
    padding: 15px 52px;
	
	&:hover {
		@include mkdfButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 13px 28px;
}

.mkdf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

.mkdf-footer-form{
    .wpcf7-form-control.wpcf7-text,
    .wpcf7-form-control.wpcf7-textarea{
        background-color: #1f1f1f;
        color: #fff;
        border: none;
        margin-bottom: 14px;
    }

    .wpcf7-form-control.wpcf7-textarea{
        height: 103px;
    }

    input.wpcf7-form-control.wpcf7-submit{
        @include mkdfButtonOutlineColor();
        color: #fff;
        padding: 17px 52px;

        &:hover{
            @include mkdfButtonOutlineHoverColor();
            color: $default-heading-color;
        }
    }
}

.newsletter{
    position: relative;
    box-shadow: 0 0 18px rgba(0,0,0, .2);

    .wpcf7-form-control{
        &.wpcf7-text{
            padding: 0 200px 0 28px;
            margin: 0;
            height: 63px;
            line-height: 63px;
            background-color: #fff;
        }

        &.wpcf7-submit{
            position: absolute;
            right: 0;
            top: 0;
            padding: 20px 55px;
        }
    }

    .ajax-loader{
        position: absolute;
        top: 25px;
        right: 200px;
    }
    
    @include phone-landscape{
        
        box-shadow: none;
        overflow:hidden;

        .wpcf7-form-control{
            &.wpcf7-submit{
                position: relative;
                box-shadow: 0 0 18px rgba(0,0,0, .2);
                top: 0;
            }
        }
        
        .ajax-loader{
            position: relative;
            top: auto;
            right: auto;
        }

        .mkdf-btn-wrapper.mkdf-btn-wrapper-nl .mkdf-btn.mkdf-btn-solid {
            padding:0 !important;
        }
    }
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.mkdf-slider-is-moving {
		
		.owl-item {
			
			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.mkdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
			@include mkdfTransform(translateY(-50%));
			@include mkdfTransition(color .2s ease-out);
			
			&:hover {
				color: $first-main-color;
			}
			
			> span,
            > i {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 40px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 40px;
		}
		
		.owl-next {
			right: 40px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 35px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 14px;
				height: 14px;
				margin: 0 6px;
				border-radius: 50%;
				background-color: transparent;
				border: 2px solid $default-heading-color;
				box-sizing: border-box;
				@include mkdfTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            img {
                border: 2px solid transparent;
                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .mkdf-wrapper {
        padding: 0 !important;
    }
    
    .mkdf-content {
        height: 100vh;
    }
	
	&.admin-bar {
		
		.mkdf-content {
			height: calc(100vh - 32px);
		}
	}

    .mkdf-content-inner {
	    @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
	    @include mkdfTableLayout();
        padding: 0 16.7%;
        text-align: left;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;

        .mkdf-404-wrapper,
        .mkdf-404-title{
            display: table-cell;
            width: 50%;
            vertical-align: middle;
        }

        .mkdf-404-title {
            margin: 0;
            font-size: 377px;
            color: #eeeeee;
            text-align: right;
        }
	
	    .mkdf-404-subtitle {
            margin: 35px 0;
            position: relative;
            padding-bottom: 25px;

            &:after{
                content: '';
                width: 68px;
                height: 5px;
                background-color: $first-main-color;
                bottom: 0;
                left: 0;
                position: absolute;
            }
        }

        h1.mkdf-404-subtitle{
            font-size: 80px;
        }
	    
	    .mkdf-404-text {
		    margin: 7px 0 0;
            font-size: 20px;
            padding-right: 10%;
            font-weight: 300;
            font-style: italic;
	    }

        .mkdf-btn {
            margin: 35px 0 0;
            padding: 21px 43px;
	
	        &.mkdf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }

    @include laptop-landscape-large{
        .mkdf-page-not-found{
            .mkdf-404-title{
                font-size: 250px;
            }
            h1.mkdf-404-subtitle{
                font-size: 50px;
            }
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }

    @include phone-landscape{
        .mkdf-page-not-found{
            
            .mkdf-404-title,
            .mkdf-404-wrapper{
                display: block;
                width: 100%;
            }

            .mkdf-404-title{
                font-size: 150px;
                text-align: left;
            }
            h1.mkdf-404-subtitle{
                font-size: 30px;
            }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 50px;
    right: 40px;
    bottom: 40px;
    margin: 0;
    background-color: $first-main-color;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include mkdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 48px;
        height: 48px;
        line-height: 46px;
        font-size: 20px;
        text-align: center;
        color: #000;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;
        @include mkdfTransition(all .3s cubic-bezier(.6,0,.26,.99));

        &:last-child {
            position: absolute;
            top: 0;
            left: 0;
            @include mkdfTransform(translateY(100%));
        }
    }

    &:hover {
        span {
            &:first-child {
                @include mkdfTransform(translateY(-100%));
            }

            &:last-child {
                @include mkdfTransform(translateY(0));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
