/* ==========================================================================
   Blog List widget style - begin
   ========================================================================== */

.widget.mkdf-blog-list-widget {
    margin: 0 0 20px;

    > h4 {
        margin: 0 0 15px;
    }

    .mkdf-blog-list-holder{
        .mkdf-blog-list{
            .mkdf-bl-item{
                &:last-child{
                    margin-bottom: 0;
                    padding-bottom: 0;
                }
            }
        }
    }

    footer .mkdf-footer-top &,
    .mkdf-top-bar & {

        > h2 {
            margin: 0 0 8px;
        }
    }
}
/* ==========================================================================
   Blog List widget style - end
   ========================================================================== */