// widgets spacing
$widget-bottom-margin: 21px;
$widget-title-margin: 10px;

// list items dimensions
$widget-list-item-height: 29px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: #999999;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #999999;

// links
$widget-link-color: #a5a5a5;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

footer {
    @import 'widgets/default-wordpress-widgets-flat';

    .mkdf-footer-bottom-holder{
        .widget{
            margin: 0;

            .textwidget{
                color: #a5a5a5;
            }
        }
    }

    .mkdf-icon-widget-holder{
        .mkdf-icon-text{
            font-size: 15px !important;
        }
    }

    .widget {
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_nav_menu {
            ul li {

                padding: 6px 0;

                a {
                    color: $widget-link-color;
                }
            }
        }

        &.widget_search {

            .input-holder {
                background-color: #222;
            }
        }
    }

    .mkdf-blog-list-holder {

        &.mkdf-bl-minimal .mkdf-post-info-date {
            margin-bottom: 15px;
        }

        .mkdf-post-title {
            line-height: 20px;
        }
    }
}