/* ==========================================================================
   Social Icons Group widget style - begin
   ========================================================================== */

.mkdf-social-icons-group-widget {

	&.mkdf-square-icons {

		.mkdf-social-icon-widget-holder {
			position: relative;
			width: 40px;
			height: 40px;
			margin: 0 7px;
			font-size: 16px;
			color: #fff;
			background-color: $default-heading-color;

			&:hover {
				color: $default-heading-color;
				background-color: $first-main-color;
			}

			.mkdf-social-icon-widget {
				position: absolute;
				top: 50%;
				left: 50%;
				@include mkdfTransform(translateY(-50%) translateX(-50%));
			}
		}

		&.mkdf-light-skin {

			.mkdf-social-icon-widget-holder {
				color: $default-heading-color;
				background-color: #fff;

				&:hover {
					color: $default-heading-color;
					background-color: $first-main-color;
				}
			}
		}
	}

	.mkdf-social-icon-widget-holder {
		display: inline-block;
		vertical-align: top;
		margin: 0 10px 0 0;
		text-align: center;
		color: $default-heading-color;
		font-size: 16px;

		&:hover{
			color: $second-main-color;
		}

		&:first-child {
			margin-left: 0;
		}

		&:last-child {
			margin-right: 0;
		}
	}

	&.mkdf-light-skin {

		.mkdf-social-icon-widget-holder {
			color: #fff;

			&:hover {
				color: $first-main-color;
			}
		}
	}
}
/* ==========================================================================
   Social Icons Group widget style - end
   ========================================================================== */